<template>
  <div :class="['choose-wrap', disabled?'disabled':'']" @click="showwrapper" v-document-click="documentClick">
    <div class="choose">
      <span>{{value}}</span>
      <i class="el-icon-caret-bottom"></i>
      <div :class="{'mt-content': true,'active':showactive}">
          <h2>{{title}}</h2>
          <div :class="['wrapper',className]">
              <div class="col" v-for="(listData, index) in renderList" :key="index">
                  <span @click="changeValue(item);overavtive()" :class="{'mt-item':true,'active':item.name == value}" v-for="(item,index) in listData" :key="index" >{{item.name}}</span>
              </div>
          </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
    props:[
        'list',
        'value',
        'title',
        'showactive',
        'disabled',
        'className'
    ],
    computed: {
        renderList: function(){
            let col = Math.ceil(this.list.length / 12 )
            let resultList = []
            for(var i = 0;i < col; i++){
                let data = this.list.slice(i * 12, i* 12 + 12)
                resultList.push(data)
            }
            return resultList
        }
    },
    methods:{
        showwrapper(e){
            e.stopPropagation()
            // this.showactive = true
            if(!this.disabled){
            this.$emit('change_active',true)
            }
        },
        documentClick(){
            // console.log('document-click')
            // this.showactive = false
            this.$emit('change_active',false)
        },
        changeValue(item){
            this.$emit('change',item)
        },
        overavtive(e){
            this.$emit('change_active')
        }
    }
};
</script>
<style lang="scss">
@import "@/assets/css/changeCity/select.scss";
</style>


