<template>
  <div>
    <span class="name">:</span>
    <myselect
      :list="provinceList"
      title=""
      :value="province"
      :showactive="provinceActive"
      @change_active="changeproActive"
      @change="changeProvince"
      className="province"
    />
    <myselect
      :list="cityList"
      title=""
      :value="city"
      :showactive="cityActive"
      @change_active="changecityActive"
      @change="changeCity"
      :disabled="cityDisabled"
      className="city"
    />
    <span>:</span>
    <el-select
      v-model="searchWord"
      filterable
      remote
      reserve-keyword
      placeholder=""
      :remote-method="remoteMethod"
      :loading="loading"
    >
      <el-option
        v-for="item in searchList"
        :key="item"
        :label="item"
        :value="item"
      >
      </el-option>
    </el-select>
  </div>
</template>
<script>
import myselect from "./select.vue";
import api from '@/api/index.js'
export default {
  data() {
    return {
      provinceList: [],
      province: "",
      cityList: [],
      city: "",
      provinceActive: false,
      cityActive: false,
      searchList:["", "", "", ""],
      searchWord:'',
      loading:false,
      cityDisabled:true
    };
  },
  components: {
    myselect
  },
  created(){
    api.getProvinceList().then(res => {
      this.provinceList = res.data.data.map(item => {
        item.name = item.provinceName
        // console.log(item)
        return item
      })
      console.log(this.provinceList)
    })
  },
  methods: {
    // 
    changeproActive(flag) {
      this.provinceActive = flag;
      if (flag) {
        this.cityActive = false;
      }
    },
    // 
    changecityActive(flag) {
      this.cityActive = flag;
      if (flag) {
        this.provinceActive = false;
      }
    },
    // 
    changeProvince(item){
        this.province = item.provinceName
        this.cityDisabled = false
        this.cityList = item.cityInfoList
    },
    // 
    changeCity(item){
        this.city = item.name
        this.$store.dispatch('setPosition',item)
        this.$router.push({name: 'index'})
    },
    remoteMethod(val){
      // console.log(val)
      // 
    }
  }
};
</script>
<style lang="scss" >
@import "@/assets/css/changeCity/iselect.scss";
</style>


