import axios from 'axios'

axios.defaults.baseURL = 'https://api.duyiedu.com'
axios.interceptors.request.use(function (config) {
  console.log(config)
  config.params = {
    ...config.params,
    appkey: 'xsk126_1565957970192'
  }
  return config
}, function (error) {
  return Promise.reject(error)
})

export default axios



