<template>
  <div class="m-product-categroy">
      <dl class="classic">
          <dt></dt>
          <dt></dt>
          <dd v-for="(item) in classList" :key="item.type">
             <myselect :name="item.title" :list="item.subList" />
          </dd>
      </dl>
      <dl class="classic">
          <dt></dt>
          <dt></dt>
          <dd v-for="(item) in areaList" :key="item.type">
             <myselect :name="item.title" :list="item.subList" />
          </dd>
      </dl>
  </div>
</template>
<script>
import myselect from './select.vue'
import api from '@/api/index.js'
export default {
  created(){
    api.getClassify().then(res => {
      // console.log(res.data)
      let classify = res.data.data
      this.classList.push(...classify)
    })
    // api.getAearList().then(res => {
    //    let a = res.data.replace(/[\r\n]/g,"").replace(/\ +/g,"").replace(/^\"|\"$/g,'')
    // })
  },
  components:{
    myselect
  },
  data() {
    return {
      classList: [],
      areaList: [{

        "title": "",
        "subList": [{
            "name": "",
            "id": 120000
        }, {
            "name": "",
            "id": 12222
        }]
    }]
    };
  }
};
</script>


