<template>
  <div class="m-products-list">
    <ul>
      <li
        :class="{ 's-nav-active': item.active }"
        v-for="item in nav"
        :key="item.key"
      >
        {{ item.name }}
      </li>
    </ul>
    <el-row>
        <Item v-for="(item,index) in products" :key="index" :meta="item"/>
    </el-row>
  </div>
</template>
<script>
import api from '@/api/index.js'
import Item from './item.vue'
export default {
  components:{
    Item
  },
  created(){
    api.getProducts().then(res => {
      console.log(res.data.data)
      this.products = res.data.data
    })
  },
  data() {
    return {
      nav: [
        {
          key: "s-default",
          name: "",
          active: true
        },
        {
          key: "s-price",
          name: "",
          active: false
        },
        {
          key: "s-score",
          name: "",
          active: false
        },
        {
          key: "s-comment",
          name: "",
          active: false
        }
      ],
      products: []
    };
  }
};
</script>


