<template>
  <dl class="s-item">
    <dt>
      <img :src="meta.image" alt="" />
    </dt>
    <dd>
      <h3>
        <router-link :to="{ name: 'productDeatil' }">{{
          meta.title
        }}</router-link>
      </h3>
      <p>
        <el-rate
          v-model="meta.score"
          disabled
          show-score
          text-color="#ff9900"
          score-template="{value}">
        </el-rate>
        <span v-if="meta.socre > 4.5"></span>
        <span v-else-if="meta.socre > 4"></span>
        <span v-else-if="meta.socre > 3.5"></span>
        <span v-else-if="meta.socre > 3"></span>
        <span v-else></span>
        <span class="">{{meta.score}}</span>
        <span class="s-item-comment-total">{{meta.commentNum}}</span>
      </p>
      <p>
          <span class="s-item-tab">
              <span v-for="(v,i) in meta.tab" :key="i">{{v + (i < meta.tab.length - 1 ? '|' : '')}}</span>
          </span>
          <span class="s-item-addr">
              {{meta.address}}
          </span>
          <span>
              <a href="#">
                  <i class="el-icon-location"></i>
                  
              </a>
          </span>
      </p>
      <p>
          <span class="s-item-price">
              {{meta.avgPrice}}
          </span>
      </p>
      <ul class="deal-items">
          <li class="items" v-for="(item,index) in meta.dealItems.slice(0,2)" :key="index">
              <p class="deal-title">{{item.title}}</p>
              <p>
                  <span class="deal-price">
                      {{item.price}}
                  </span>
                  <span class="deal-old-price">{{item.counterPrice}}</span>
                  <span class="deal-sales">{{item.saleNum}}</span>
              </p>
          </li>
      </ul>
    </dd>
  </dl>
</template>
<script>
export default {
  props: ["meta"]
};
</script>


